"use strict";
import 'babel-polyfill'

/* Vue */
import Vue from 'vue'
import VueRouter from 'vue-router';
import Routers from './router/router.js';

import App from './app.vue';

/* vue */
import store from './vuex'


Vue.use(VueRouter);

// 
const RouterConfig = {
    routes: Routers
};

const router = new VueRouter(RouterConfig);

new Vue({
    el: '#app',
    router: router,
    store,
    render: h => h(App)
});



